"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  Settings, 
  Users, 
  User,
  Wrench, 
  FileText, 
  Search, 
  MoreVertical,
  Moon,
  Sun,
  LogOut
} from "lucide-react";

// Mock data
const mockUsers = [
  { id: 1, name: "Ahmet Yılmaz", phone: "05551234567", type: "user", joinDate: "2024-01-15", status: "active" },
  { id: 2, name: "Fatma Kaya", phone: "05559876543", type: "user", joinDate: "2024-01-20", status: "active" },
  { id: 3, name: "Mehmet Demir", phone: "05557654321", type: "user", joinDate: "2024-02-01", status: "suspended" },
];

const mockMasters = [
  { id: 1, name: "Mehmet Usta", phone: "05551111111", profession: "Elektrikçi", rating: 4.8, jobsCompleted: 45, joinDate: "2023-12-01", status: "active" },
  { id: 2, name: "Ali Usta", phone: "05552222222", profession: "Tesisatçı", rating: 4.6, jobsCompleted: 32, joinDate: "2023-11-15", status: "active" },
  { id: 3, name: "Hasan Usta", phone: "05553333333", profession: "Boyacı", rating: 4.9, jobsCompleted: 67, joinDate: "2023-10-20", status: "active" },
];

const mockJobs = [
  { id: 1, title: "Banyo Fayans İşi", category: "Fayansçı", employer: "Ahmet Yılmaz", budget: "2000-3000 ₺", status: "active", date: "2024-01-15" },
  { id: 2, title: "Elektrik Tesisatı", category: "Elektrikçi", employer: "Fatma Kaya", budget: "1500-2500 ₺", status: "completed", date: "2024-01-10" },
  { id: 3, title: "Duvar Boyası", category: "Boyacı", employer: "Mehmet Demir", budget: "800-1200 ₺", status: "cancelled", date: "2024-01-05" },
];

export default function AdminDashboard() {
  const router = useRouter();
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);
    document.documentElement.classList.toggle('dark');
  };

  const getStatusBadge = (status: string) => {
    const statusConfig = {
      active: { label: "Aktif", variant: "default" as const },
      suspended: { label: "Askıya Alındı", variant: "destructive" as const },
      completed: { label: "Tamamlandı", variant: "secondary" as const },
      cancelled: { label: "İptal Edildi", variant: "outline" as const },
    };
    
    const config = statusConfig[status as keyof typeof statusConfig] || statusConfig.active;
    return <Badge variant={config.variant}>{config.label}</Badge>;
  };

  return (
    <div className={`min-h-screen ${isDarkMode ? 'dark bg-gray-900' : 'bg-gradient-to-br from-blue-50 to-white'}`}>
      {/* Header */}
      <div className={`${isDarkMode ? 'bg-gray-800 border-gray-700' : 'bg-white border-gray-200'} border-b shadow-sm`}>
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="w-10 h-10 bg-red-500 rounded-full flex items-center justify-center">
                <Settings className="h-6 w-6 text-white" />
              </div>
              <div>
                <h1 className={`text-xl font-bold ${isDarkMode ? 'text-white' : 'text-kova-dark'}`}>Admin Panel</h1>
                <p className={`text-sm ${isDarkMode ? 'text-gray-300' : 'text-muted-foreground'}`}>Sistem Yönetimi</p>
              </div>
            </div>
            
            <div className="flex items-center space-x-4">
              <Button
                variant="ghost"
                size="sm"
                onClick={toggleTheme}
                className={isDarkMode ? 'text-gray-300 hover:text-white' : ''}
              >
                {isDarkMode ? <Sun className="h-4 w-4" /> : <Moon className="h-4 w-4" />}
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={() => router.push("/")}
                className={isDarkMode ? 'text-gray-300 hover:text-white' : ''}
              >
                <LogOut className="h-4 w-4" />
                Çıkış
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto p-6">
        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className={`text-sm font-medium ${isDarkMode ? 'text-gray-300' : ''}`}>
                Toplam Kullanıcı
              </CardTitle>
              <Users className={`h-4 w-4 ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`} />
            </CardHeader>
            <CardContent>
              <div className={`text-2xl font-bold ${isDarkMode ? 'text-white' : ''}`}>{mockUsers.length}</div>
              <p className={`text-xs ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>
                Aktif kullanıcı sayısı
              </p>
            </CardContent>
          </Card>

          <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className={`text-sm font-medium ${isDarkMode ? 'text-gray-300' : ''}`}>
                Toplam Usta
              </CardTitle>
              <Wrench className={`h-4 w-4 ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`} />
            </CardHeader>
            <CardContent>
              <div className={`text-2xl font-bold ${isDarkMode ? 'text-white' : ''}`}>{mockMasters.length}</div>
              <p className={`text-xs ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>
                Kayıtlı usta sayısı
              </p>
            </CardContent>
          </Card>

          <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className={`text-sm font-medium ${isDarkMode ? 'text-gray-300' : ''}`}>
                Toplam İlan
              </CardTitle>
              <FileText className={`h-4 w-4 ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`} />
            </CardHeader>
            <CardContent>
              <div className={`text-2xl font-bold ${isDarkMode ? 'text-white' : ''}`}>{mockJobs.length}</div>
              <p className={`text-xs ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>
                Yayınlanan ilan sayısı
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Search */}
        <div className="mb-6">
          <div className="relative max-w-md">
            <Search className={`absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`} />
            <Input
              placeholder="Ara..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className={`pl-10 ${isDarkMode ? 'bg-gray-800 border-gray-700 text-white' : ''}`}
            />
          </div>
        </div>

        {/* Tabs */}
        <Tabs defaultValue="users" className="space-y-6">
          <TabsList className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
            <TabsTrigger value="users" className={isDarkMode ? 'data-[state=active]:bg-gray-700' : ''}>
              <Users className="h-4 w-4 mr-2" />
              Kullanıcılar
            </TabsTrigger>
            <TabsTrigger value="masters" className={isDarkMode ? 'data-[state=active]:bg-gray-700' : ''}>
              <Wrench className="h-4 w-4 mr-2" />
              Ustalar
            </TabsTrigger>
            <TabsTrigger value="jobs" className={isDarkMode ? 'data-[state=active]:bg-gray-700' : ''}>
              <FileText className="h-4 w-4 mr-2" />
              İlanlar
            </TabsTrigger>
          </TabsList>

          {/* Users Tab */}
          <TabsContent value="users">
            <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
              <CardHeader>
                <CardTitle className={isDarkMode ? 'text-white' : ''}>Kullanıcılar</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {mockUsers.filter(user => 
                    user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                    user.phone.includes(searchTerm)
                  ).map((user) => (
                    <div key={user.id} className={`flex items-center justify-between p-4 rounded-lg border ${isDarkMode ? 'border-gray-700 bg-gray-750' : 'border-gray-200'}`}>
                      <div className="flex items-center space-x-4">
                        <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
                          <User className="h-5 w-5 text-blue-600" />
                        </div>
                        <div>
                          <h3 className={`font-medium ${isDarkMode ? 'text-white' : ''}`}>{user.name}</h3>
                          <p className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>{user.phone}</p>
                          <p className={`text-xs ${isDarkMode ? 'text-gray-500' : 'text-muted-foreground'}`}>Katılım: {user.joinDate}</p>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        {getStatusBadge(user.status)}
                        <Button variant="ghost" size="sm">
                          <MoreVertical className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Masters Tab */}
          <TabsContent value="masters">
            <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
              <CardHeader>
                <CardTitle className={isDarkMode ? 'text-white' : ''}>Ustalar</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {mockMasters.filter(master => 
                    master.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                    master.profession.toLowerCase().includes(searchTerm.toLowerCase())
                  ).map((master) => (
                    <div key={master.id} className={`flex items-center justify-between p-4 rounded-lg border ${isDarkMode ? 'border-gray-700 bg-gray-750' : 'border-gray-200'}`}>
                      <div className="flex items-center space-x-4">
                        <div className="w-10 h-10 bg-primary rounded-full flex items-center justify-center">
                          <Wrench className="h-5 w-5 text-primary-foreground" />
                        </div>
                        <div>
                          <h3 className={`font-medium ${isDarkMode ? 'text-white' : ''}`}>{master.name}</h3>
                          <p className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>{master.profession} • ⭐ {master.rating}</p>
                          <p className={`text-xs ${isDarkMode ? 'text-gray-500' : 'text-muted-foreground'}`}>{master.jobsCompleted} iş tamamlandı • Katılım: {master.joinDate}</p>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        {getStatusBadge(master.status)}
                        <Button variant="ghost" size="sm">
                          <MoreVertical className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Jobs Tab */}
          <TabsContent value="jobs">
            <Card className={isDarkMode ? 'bg-gray-800 border-gray-700' : ''}>
              <CardHeader>
                <CardTitle className={isDarkMode ? 'text-white' : ''}>İlanlar</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {mockJobs.filter(job => 
                    job.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                    job.category.toLowerCase().includes(searchTerm.toLowerCase())
                  ).map((job) => (
                    <div key={job.id} className={`flex items-center justify-between p-4 rounded-lg border ${isDarkMode ? 'border-gray-700 bg-gray-750' : 'border-gray-200'}`}>
                      <div className="flex items-center space-x-4">
                        <div className="w-10 h-10 bg-green-100 rounded-full flex items-center justify-center">
                          <FileText className="h-5 w-5 text-green-600" />
                        </div>
                        <div>
                          <h3 className={`font-medium ${isDarkMode ? 'text-white' : ''}`}>{job.title}</h3>
                          <p className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-muted-foreground'}`}>{job.category} • {job.budget}</p>
                          <p className={`text-xs ${isDarkMode ? 'text-gray-500' : 'text-muted-foreground'}`}>İşveren: {job.employer} • {job.date}</p>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        {getStatusBadge(job.status)}
                        <Button variant="ghost" size="sm">
                          <MoreVertical className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
